import React from 'react';
import { useParams } from 'react-router-dom';
import Shoes from './../shoes.json';
import './../App.css'
function ProductItem() {
  const { id } = useParams();
  const shoe = Shoes[id];
  if (!shoe)
    return <h2>Product Not Found</h2>
  return (
    <div>
      <h1>ProductItem</h1>

      <div  className="link" >
        <h2>{shoe.name}</h2>
      <h6 className="desc">{shoe.desc}</h6>
        <div>
        <img src={shoe.img} height={500} />
        <img src={shoe.img1} height={500}  />
        <img src={shoe.img2} height={500}  />
        <img src={shoe.img3} height={500}  />
          
        </div>
      </div>
    </div>
  );
}



export default ProductItem;
