import React from 'react';
import {Link} from 'react-router-dom';
import './../App.css'

function Navbar() {
  return (
    <div className="Nav">
        <Link className="pehlalink" to='/'>  Home </Link>
        <Link className="pehlalink" to='/about'>    About  </Link>
        <Link className="pehlalink" to='/product'>   Product  </Link>
   
    </div>
  );
}



export default Navbar;
